				var res = [...ra.data]
				console.log(res)
				res.forEach(item => {
					item.updated = item.updated.slice(0, 10);
					for (let i in item.img) {
						item.image = item.img[i]
					}
				})
				if (index == 1) {
					const data = [...this.bottom_Alldata]
					res.forEach(item => {
						data.push(item)
					})
					this.bottom_Alldata = data;
					uni.hideLoading()
					return false;
				}

				this.bottom_Alldata = res;
				uni.hideLoading()
			})
